import styles from './custom-button.module.scss';

export default function CustomButton({ text, className}) {
  const handleScrollToTarget = () => {
    const targetElement = document.getElementById('partner-block');
    if (targetElement) {
      targetElement.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <a href="https://t.me/evgeniydeg" target="_blank" rel="noopener noreferrer">
      <button className={`${styles.button} ${className}`} onClick={handleScrollToTarget}>
        {text}
      </button>
    </a>
  );
}